import { createStore } from "vuex";
import getWeb3 from "@/utils/getWeb3";

export default createStore({
  state: {
    isInstallMetaMask: null,
    account: null,
    balance: null,
    network: null,
    isConnectWallet: null,
    isOpenWalletModal: false,
    isOpenBuyTicketModal: false,
  },
  getters: {},
  mutations: {
    setIsInstallMetaMask(state, payload) {
      state.isInstallMetaMask = payload;
    },
    setAccount(state, payload) {
      state.account = payload.account;
    },
    setBalance(state, payload) {
      state.balance = payload.balance;
    },
    setNetwork(state, payload) {
      state.network = payload.network;
    },
    setConnectWallet(state, payload) {
      state.isConnectWallet = payload;
    },
    setIsOpenWalletModal(state, payload) {
      state.isOpenWalletModal = payload;
    },
    setIsOpenBuyTicketModal(state, payload) {
      state.isOpenBuyTicketModal = payload;
    },
  },
  actions: {
    checkInstallMetaMask({ commit }) {
      const { ethereum } = window;
      commit("setIsInstallMetaMask", Boolean(ethereum && ethereum.isMetaMask));
    },
    async setWeb3({ commit }) {
      const web3 = await getWeb3();
      const accounts = await web3.eth.getAccounts();
      if (accounts.length > 0) {
        window.ethereum.on("accountsChanged", function (accounts) {
          commit("setAccount", { account: accounts[0] });
          if (accounts.length == 0) {
            commit("setConnectWallet", false);
            localStorage.removeItem("isConnectWallet");
          }
        });
        window.ethereum.on("chainChanged", function (networkId) {
          commit("setNetwork", { network: networkId });
        });
        const account = accounts[0];
        commit("setAccount", { account: account });
      } else {
        commit("setConnectWallet", false);
        localStorage.removeItem("isConnectWallet");
      }
    },
    async getBalance({ commit, state }) {
      const web3 = await getWeb3();
      const account = state.account;
      await web3.eth.getBalance(account).then((value) => {
        commit("setBalance", { balance: web3.utils.fromWei(value, "ether") });
      });
    },
    async getNetwork({ commit }) {
      await window.ethereum.request({ method: "eth_chainId" }).then((value) => {
        commit("setNetwork", { network: parseInt(value, 16) });
      });
    },
    setIsConnectWallet({ commit }, payload) {
      commit("setConnectWallet", payload);
    },
    setIsOpenWalletModal({ commit }, payload) {
      commit("setIsOpenWalletModal", payload);
    },
    setIsOpenBuyTicketModal({ commit }, payload) {
      commit("setIsOpenBuyTicketModal", payload);
    },
  },
});
