<script>
import {
  mdiPhoneInTalk,
  mdiEarth,
  mdiWhiteBalanceSunny,
  mdiWeatherNight,
  mdiMenu,
} from "@mdi/js";
import LanguageSwitch from "@/components/LanguageSwitch/LanguageSwitch.vue";
import { mapActions, mapState } from "vuex";
import Divider from "@/components/Divider/Divider.vue";
import connectMetaMask from "@/utils/connectMetaMask";
import addNetwork from "@/utils/addNetwork";
import WalletButton from "@/components/WalletButton/WalletButton.vue";
import WalletModal from "../WalletModal/WalletModal.vue";

export default {
  name: "Navbar",
  data() {
    return {
      view: {
        topOfPage: true,
        isOpenLanguageMenu: false,
        info: null,
        isOpenMenuMobile: false,
      },
      icons: {
        mdiPhoneInTalk,
        mdiEarth,
        mdiWhiteBalanceSunny,
        mdiWeatherNight,
        mdiMenu,
      },
      images: {
        logo: require("@/assets/images/penguin.png"),
        ticket: require("@/assets/images/tag.png"),
      },
    };
  },
  computed: {
    ...mapState([
      "isInstallMetaMask",
      "balance",
      "account",
      "network",
      "isConnectWallet",
    ]),
  },
  beforeMount() {
    window.addEventListener("scroll", this.handleScroll);
    const theme = localStorage.getItem("darkTheme");
    if (theme) {
      if (theme === "true") {
        this.$vuetify.theme.dark = true;
      } else {
        this.$vuetify.theme.dark = false;
      }
    } else if (
      window.matchMedia &&
      window.matchMedia("(prefers-color-scheme: dark)").matches
    ) {
      this.$vuetify.theme.dark = false;
      localStorage.setItem("darkTheme", this.$vuetify.theme.dark.toString());
    }
  },
  async mounted() {
    this.checkInstallMetaMask();
    await this.setWeb3();
    await this.getBalance();
    await this.getNetwork();
  },
  methods: {
    handleScroll() {
      if (window.pageYOffset > 0) {
        if (this.view.topOfPage) this.view.topOfPage = false;
      } else {
        if (!this.view.topOfPage) this.view.topOfPage = true;
      }
    },
    handleClickLanguage() {
      this.view.isOpenLanguageMenu = !this.view.isOpenLanguageMenu;
      console.log(this.view.isOpenLanguageMenu);
    },
    async handleConnectWallet() {
      try {
        await connectMetaMask();
        await addNetwork();
        await this.setWeb3();
        await this.getBalance();
        await this.getNetwork();
        localStorage.setItem("isConnectWallet", true);
        this.setIsConnectWallet(true);
      } catch (error) {
        console.log(error);
      }
    },
    ...mapActions({
      setWeb3: "setWeb3",
      getBalance: "getBalance",
      getNetwork: "getNetwork",
      checkInstallMetaMask: "checkInstallMetaMask",
      setIsConnectWallet: "setIsConnectWallet",
    }),
    toggleDarkMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
      localStorage.setItem("darkTheme", this.$vuetify.theme.dark.toString());
    },
    handleOpenMenuMobile() {
      this.view.isOpenMenuMobile = !this.view.isOpenMenuMobile;
      // if (this.view.isOpenMenuMobile) document.body.style.position = "fixed";
      // else document.body.style.position = null;
    },
  },

  components: { LanguageSwitch, Divider, WalletButton, WalletModal },
};
</script>

