<template>
  <div
    @click="handleOpen"
    class="nice-select"
    :class="{
      open: views.isOpenLanguageSwitch,
      lightTheme: !this.$vuetify.theme.dark,
    }"
  >
    <span class="current">{{ data.currentLanguage }}</span>
    <ul class="list">
      <li class="option" @click="handleChangeLanguage('English')">English</li>
      <li class="option" @click="handleChangeLanguage('Francais')">Francais</li>
      <li class="option" @click="handleChangeLanguage('Tiếng Việt')">
        Tiếng Việt
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "LanguageSwitch",
  data() {
    return {
      views: {
        isOpenLanguageSwitch: false,
      },
      data: {
        currentLanguage: "English",
      },
    };
  },
  methods: {
    handleOpen() {
      this.views.isOpenLanguageSwitch = !this.views.isOpenLanguageSwitch;
    },
    handleChangeLanguage(value) {
      this.data.currentLanguage = value;
    },
    close(e) {
      if (!this.$el.contains(e.target)) {
        this.views.isOpenLanguageSwitch = false;
      }
    },
  },
  mounted() {
    document.addEventListener("click", this.close);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.close);
  },
  components: {},
};
</script>
