<template>
  <div
    class="modal"
    v-show="isOpenWalletModal"
    :class="{ light: !this.$vuetify.theme.dark }"
  >
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title mx-auto">Your Wallet</h5>
          <v-icon
            :icon="icons.mdiClose"
            size="28"
            @click="handleCloseModal"
            style="cursor: pointer"
          />
        </div>
        <div class="modal-footer">
          <div class="copy-alert" v-if="views.isViewCopied">Copied!</div>
          <div v-if="network != 137">
            <div>
              You're connected to the wrong network.
              <br />Please switch to Polygon!
            </div>
            <button class="learn-how-btn">
              Learn How <v-icon :icon="icons.mdiHelpCircle" size="20" />
            </button>
          </div>
          <div v-else>
            <div class="switch">
              <div
                class="wallet-switch"
                :class="{ active: views.activeSwitch == 0 }"
                @click="views.activeSwitch = 0"
              >
                Wallet
              </div>
              <div
                class="transactions-switch"
                :class="{ active: views.activeSwitch == 1 }"
                @click="views.activeSwitch = 1"
              >
                Transactions
              </div>
            </div>
            <div v-if="views.activeSwitch == 0" class="information">
              <div class="address-title">YOUR ADDRESS</div>
              <div class="address-value-icon">
                <input type="text" class="address-value" v-model="account" />
                <v-icon
                  :icon="icons.mdiContentCopy"
                  size="18"
                  @click="handleCopyAddress"
                  style="cursor: pointer"
                  class="copy-icon"
                />
              </div>
              <div class="balance">
                <div class="balance-title">Your Balance</div>
                <div class="balance-value">{{ balance + " MATIC" }}</div>
              </div>
              <div>
                <a
                  :href="'https://bscscan.com/address/' + account"
                  class="view-on-bscscan"
                  target="_blank"
                  >View On BscScan
                </a>
              </div>
              <div>
                <button class="learn-how-btn" @click="handleLogout">
                  Disconnect Wallet
                </button>
              </div>
            </div>
            <div v-else class="recent-transactions">
              <div class="recent-transactions-title">RECENT TRANSACTIONS</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { mdiClose, mdiHelpCircle, mdiContentCopy } from "@mdi/js";
export default {
  name: "WalletModal",
  methods: {
    ...mapActions({
      setIsOpenWalletModal: "setIsOpenWalletModal",
      setIsConnectWallet: "setIsConnectWallet",
    }),

    handleCopyAddress() {
      navigator.clipboard.writeText(this.account);
      this.views.isViewCopied = true;
      setTimeout(() => {
        this.views.isViewCopied = false;
      }, 1000);
    },
    handleCloseModal() {
      this.setIsOpenWalletModal(false);
      document.body.style.overflow = "auto";
    },
  },
  data() {
    return {
      icons: { mdiClose, mdiHelpCircle, mdiContentCopy },
      views: {
        activeSwitch: 0,
        isViewCopied: false,
      },
    };
  },
  computed: {
    ...mapState(["balance", "account", "network", "isOpenWalletModal"]),
  },
};
</script>

