<template>
  <div
    @click="handleOpen"
    class="wallet-button"
    :class="{ open: views.isOpenWalletButton }"
  >
    <div
      class="address"
      :class="{ lightAddressValue: !this.$vuetify.theme.dark }"
    >
      <div
        class="icon-container"
        v-if="network == 137"
        style="border: 3px rgb(31, 199, 212) solid !important"
        :class="{ lightIconContainer: !this.$vuetify.theme.dark }"
      >
        <v-icon :icon="icons.mdiWallet" size="18" color="rgb(31, 199, 212)" />
      </div>
      <div
        class="icon-container"
        v-else
        style="border: 3px rgb(237, 75, 158) solid !important"
      >
        <v-icon :icon="icons.mdiAlert" size="18" color="rgb(237, 75, 158)" />
      </div>
      <div class="value" v-if="network == 137">{{ address }}</div>
      <div class="value" v-else>Network</div>
      <v-icon
        :icon="icons.mdiChevronDown"
        size="18"
        :class="{ lightIcon: !this.$vuetify.theme.dark }"
      />
    </div>

    <ul class="list" :class="{ lightList: !this.$vuetify.theme.dark }">
      <li
        class="option"
        @click="handleOpenWalletModel"
        :class="{ lightOption: !this.$vuetify.theme.dark }"
      >
        Wallet
        <v-icon
          v-if="network != 137"
          :icon="icons.mdiAlert"
          size="18"
          color="rgb(237, 75, 158)"
        />
      </li>
      <li class="option" :class="{ lightOption: !this.$vuetify.theme.dark }">
        Recent Transactions
      </li>
      <li
        class="option"
        @click="handleLogout"
        :class="{ lightOption: !this.$vuetify.theme.dark }"
      >
        Disconnect<v-icon :icon="icons.mdiLogoutVariant" size="18" />
      </li>
    </ul>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import { mdiWallet, mdiChevronDown, mdiLogoutVariant, mdiAlert } from "@mdi/js";
export default {
  name: "WalletButton",
  data() {
    return {
      views: {
        isOpenWalletButton: false,
      },
      icons: {
        mdiWallet,
        mdiChevronDown,
        mdiLogoutVariant,
        mdiAlert,
      },
    };
  },
  computed: {
    ...mapState(["isInstallMetaMask", "balance", "account", "network"]),
    address: function () {
      return (
        this.account.slice(0, 2) +
        "..." +
        this.account.slice(this.account.length - 4, this.account.length)
      );
    },
  },
  created() {
    if (localStorage.getItem("isConnectWallet") == "true")
      this.setIsConnectWallet(true);
    else this.setIsConnectWallet(false);
  },
  methods: {
    ...mapActions({
      logout: "logout",
      setIsConnectWallet: "setIsConnectWallet",
      setIsOpenWalletModal: "setIsOpenWalletModal",
    }),
    handleOpen() {
      this.views.isOpenWalletButton = !this.views.isOpenWalletButton;
    },
    close(e) {
      if (!this.$el.contains(e.target)) {
        this.views.isOpenWalletButton = false;
      }
    },
    handleLogout() {
      localStorage.removeItem("isConnectWallet");
      this.setIsConnectWallet(false);
    },
    handleOpenWalletModel() {
      this.setIsOpenWalletModal(true);
      document.body.style.overflow = "hidden";
    },
  },
  mounted() {
    document.addEventListener("click", this.close);
  },
  beforeUnmount() {
    document.removeEventListener("click", this.close);
  },
  components: {},
};
</script>

