  min-height: 40px;
  line-height: 47px;
  border: none;
  box-sizing: border-box;
  cursor: pointer;
  display: block;
  height: 42px;
  position: relative;
  text-align: left !important;
  white-space: nowrap;
  .address {
    background: rgb(53, 53, 71);
    border-radius: 30px;
    position: relative;
    height: 40px;
    width: 148px;
    display: flex;
    flex-direction: row;
    align-items: center;
    border: 1px #999 solid;
    justify-content: center;
    .value {
      font-weight: 600;
      font-size: 16px;
      margin-left: 32px;
      margin-right: 4px;
    }
    .icon-container {
      width: 40px;
      height: 40px;
      box-sizing: border-box;
      background-color: rgb(8, 6, 11);
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      border-radius: 30px;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .option {
    cursor: pointer;
    font-weight: 400;
    line-height: 50px;
    list-style: none;
    min-height: 40px;
    outline: none;
    padding-left: 20px;
    padding-right: 29px;
    text-align: left;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
  }
  .list {
    visibility: hidden;
    background-color: rgb(39, 38, 44);
    box-shadow: 0px 3px 7px 0px rgb(0 0 0 / 35%);
    width: 260px;
    border-radius: 16px;
    box-sizing: border-box;
    margin-top: 10px;
    overflow: hidden;
    position: absolute;
    top: 100%;
    right: 0;
    transform-origin: 50% 0;
    transition: all 0.15s cubic-bezier(0.5, 0, 0, 1.25);
    transform: scale(0.75) translateY(-21px);
    z-index: 9999;
  }
}
.lightAddressValue {
  background: #f1f4f6 !important;
  .value {
    color: #000;
  }
}
.lightIcon {
  color: #000;
}
.lightIconContainer {
  background-color: #f1f4f6 !important;
}
.wallet-button .wallet-button .option:hover {
  background: rgb(53, 53, 71);
}

.wallet-button.open .list {
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
  transform: scale(1) translateY(0);
}
.lightList {
  background-color: #fff !important;
}
.lightOption {
  color: #000;
}
